import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import styles from "./ProjectDetail.module.scss";

const ProjectDetail = () => {
  console.log("ProjectDetail 컴포넌트 시작");
  const { id } = useParams();
  console.log("useParams에서 받은 id:", id);
  const [project, setProject] = useState(null);

  useEffect(() => {
    console.log("useEffect 실행");

    // 모든 ScrollTrigger 인스턴스 제거
    ScrollTrigger.getAll().forEach((trigger) => trigger.kill());

    // 스크롤 위치 초기화
    window.scrollTo(0, 0);

    // body의 overflow 스타일 초기화
    document.body.style.overflow = "";

    const fetchedProject = {
      id: id,
      title: `프로젝트 ${id}`,
      description: `프로젝트 ${id}에 대한 상세 설명입니다.`,
    };
    console.log("fetchedProject:", fetchedProject);
    setProject(fetchedProject);

    // 컴포넌트 언마운트 시 cleanup
    return () => {
      // 필요한 경우 여기에 추가적인 cleanup 로직을 넣을 수 있습니다.
    };
  }, [id]);

  console.log("현재 project 상태:", project);

  if (!project) {
    console.log("project가 null이어서 로딩 중 표시");
    return <div>로딩 중...</div>;
  }

  console.log("ProjectDetail 렌더링");
  return (
    <div className={styles.projectDetail}>
      <div className={styles.content}>
        <h1>{project.title}</h1>
        <p>{project.description}</p>
        <p>프로젝트 ID: {id}</p>
      </div>
    </div>
  );
};

export default ProjectDetail;
